body {
  background-image: -webkit-gradient(linear, left top, right top, from(#250137), color-stop(#33005c), color-stop(#3c0085), to(#3900b3));
  background-image: -webkit-linear-gradient(left, #250137, #33005c, #3c0085, #3900b3);
  background-image: -o-linear-gradient(left, #250137, #33005c, #3c0085, #3900b3);
  background-image: linear-gradient(to right, #250137, #33005c, #3c0085, #3900b3);
}

.particles {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}



